.two-column-list__container.layout-container {
  margin-top: 0;
  position: relative;

  &:not(.-no-moreLink) {
    padding-bottom: unit(0.5);
  }

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    margin-bottom: unit(0.5);
  }

  [class*='heading-'] {
    text-align: center;
  }
}

.two-column-list {
  margin: unit(1.5) auto;
  max-width: 98rem;

  @include respond-to('medium-large') {
    align-items: flex-start;
    display: flex;
    justify-content: space-between;
  }
}

.two-column-list__list {
  @include reset-list;
  margin: 0;

  &:first-child li:first-child {
    padding-top: 0;
  }

  @include respond-to('medium-large') {
    @include clearfix;
    flex: 0 0 calc(50% - 50px);
  }
}

.two-column-list__item {
  border-bottom: 1px solid $border--light;
  line-height: 1.4;
  margin: 0;

  @include respond-to('medium-large') {
    &:first-child {
      padding-top: 0;
    }
  }

  a {
    @include hover-underline;
    color: $color--venice-blue;
    display: block;
    padding: unit(1) unit(0.25);
    text-align: left;
    transition: background 0.3s $ease--snappy;

    @include hover {
      background-color: rgba($color--jelly-bean, 0.1);
      text-decoration: none;
    }
  }

  span {
    display: block;
    padding: unit(1) unit(0.25);
    text-align: left;
  }
}

.two-column-list__button {
  bottom: 0;
  left: 50%;
  position: absolute;
  transform: translate3d(-50%, 50%, 0);
}
